import React from "react";
import Select from "react-select";
import { selectEmailOptions } from "../../../../appConfig";
import { Accordion, Col, Form } from "react-bootstrap";

const Email = ({ emailValues, onChangeHandler, setterFunc }) => {
  return (
    <Accordion.Item eventKey="5" className="mt-3 border">
      <Accordion.Header>E-mail</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Select
            defaultValue={emailValues}
            isMulti
            name="Email"
            options={selectEmailOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={(data) =>
              onChangeHandler(data, setterFunc, "emailValues")
            }
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default Email;
