import React, { useEffect, useState } from "react";

import { borderStyles } from "../../helpers/utils";
import DraggableTextField from "../DraggableTextField";
import AddNewModule from "./AddNewModule";
import BackArrow from "../../icons/BackArrow";
import { useNavigate } from "react-router-dom";
import { errorToast } from "../../helpers/toast";
import { workflowAPI } from "../../services/workflowApi";

const Sidebar = ({pageList, moduleList, workflowName }) => {
  const [optionsList, setOptionsList] = useState([]);
  const [pageOptionsList, setPageOptionsList] = useState([])
  const navigate = useNavigate();
  const companyName = sessionStorage.getItem("companyName");

  const addNewHandler = (value, setValue) => {
    const isModuleNameExist = moduleList?.some(
      (module) => module.name === value
    );
    if (!isModuleNameExist) {
      const newOption = {
        type: "custom",
        data: { label: value },
      };
      setOptionsList((prevState) => [...prevState, newOption]);
      setValue("");
    } else {
      errorToast("Module name must be different from existing module names.");
    }
  };

  const getWorkflowModuleList = async () => {
    const workflowModuleList = await workflowAPI.fetchWorkflowModuleList();

    const sidebarModuleList = await workflowModuleList.concat(
      moduleList.filter(
        (obj2) =>
          !workflowModuleList.some(
            (obj1) => obj1.data.label === obj2.data.label
          )
      )
    );

    const uniqueLabels = {};
    const uniqueArray = sidebarModuleList.filter((item) => {
      const label = item.data.label;
      if (!uniqueLabels[label]) {
        uniqueLabels[label] = true;
        return true;
      }
      return false;
    });

    let modifyData = moduleList.map((item) => ({
      data: {
        label: item.data.label,
      },
      type: item.type,
    }));
    let margeData = [...modifyData, ...uniqueArray];

    const filterDuplicateModule = margeData.reduce((acc, curr) => {
      const isNameAlreadyAdded = acc.find(
        (item) => item.data.label === curr.data.label
      );
      if (!isNameAlreadyAdded) {
        acc.push(curr);
      }

      return acc;
    }, []);

    setOptionsList(filterDuplicateModule);
  };

  useEffect(()=>{
    setPageOptionsList(pageList)
    console.log('pageoptionslist is:', pageOptionsList)
  },[pageList])

  useEffect(() => {
    getWorkflowModuleList();
  }, [moduleList]);

  return (
    <div className='sidebarContainer'>
      <div className="d-flex justify-content-between" onClick={() => navigate(`/?companyName=${btoa(companyName)}`)}>
        <BackArrow />
      {workflowName && <h2 className='sidebarHeading'>{workflowName}</h2>}
      </div>
      <hr />
      <h2 className='sidebarHeading mt-2' >Pages</h2>

      {pageOptionsList &&
        pageOptionsList.data?.length > 0 &&
        pageOptionsList?.data?.map((item, index) => (
          <>
          <DraggableTextField
            key={index}
            
            fieldName={item?.page_name}
            item={item}
          />
          </>
        ))}

      {/* <AddNewModule addNewHandler={addNewHandler} /> */}
    </div>
  );
};

export default Sidebar;
