import React, { useState } from "react";
import { Col, Form, Row } from "react-bootstrap";
import Select from "react-select";

import CustomSelect from "../../../../Form/Select";
import {
  selectDateOptions,
  selectDefaultOptions,
  selectFileTypeOptions,
  selectValidationOptions,
} from "../../../../../appConfig";
import InputText from "../../../../Form/InputText";
import PlusCircle from "../../../../../icons/PlusCircle";
import MinusCircle from "../../../../../icons/MinusCircle";
import OtpOptions from "./OtpOptions";
import PanOptions from "./PanOptions";
import Prepopulate from "./Prepopulate";
import TypeSection from "./TypeSection";
import MobileEmail from "./MobileEmail";
import { capitalizeFirstLetter } from "../../../../../helpers/utils";

const ExpandedFields = (props) => {
  const {
    field,
    onChangeHandler,
    uniqueKeyList,
    uniqueKeyListHandler,
    workflowFieldsList,
    fields,
  } = props;

  const [selectOptions, setSelectOptions] = useState(
    field.values || selectDefaultOptions
  );

  const addNewOption = () => {
    const lastOption = selectOptions[selectOptions.length - 1];
    const newOption = {
      key: lastOption.key + 1,
      label: `Option ${lastOption.key + 1}`,
    };
    setSelectOptions([...selectOptions, newOption]);
  };

  const deleteOption = (e, idx, fieldId) => {
    const filteredOptions = selectOptions.filter(
      (option, index) => index !== idx
    );
    setSelectOptions(filteredOptions);
    onChangeHandler(e, "values", fieldId, "optionDelete");
  };

  const getFieldsNames = () => {
    return fields.map((item) => item.name);
  };
  const fieldsNames = getFieldsNames();

  return (
    <Row className="mb-3 mt-4 row">
      <TypeSection field={field} onChangeHandler={onChangeHandler} />

      {[
        "number",
        "text",
        "button",
        "submit",
        "hidden",
        "checkbox",
        "multiSelectCheckbox",
        "password",
        "email",
      ].includes(field.type) && (
        <CustomSelect
          label={"Validation type"}
          value={field.validation || "no"}
          fieldName={"validation"}
          fieldId={field.id}
          onChangeHandler={onChangeHandler}
          options={selectValidationOptions}
          col={"5"}
        />
      )}

      <Form.Group className="mb-3" as={Col} md="3">
        <Form.Label>{"Revalidate"}</Form.Label>
        <Form.Check
          className="custom-checkbox-lg"
          checked={field?.revalidate || false}
          onChange={(e) => onChangeHandler(e, "revalidate", field.id)}
        />
      </Form.Group>

      <Form.Group className="mb-3" as={Col} md="4">
        <Form.Label>Revalidate with</Form.Label>
        <Form.Select
          disabled={!field?.revalidate}
          value={field?.validateWith}
          onChange={(e) => onChangeHandler(e, "validateWith", field.id)}
        >
          <option>Select</option>
          {fieldsNames?.map((item, index) => (
            <option key={index} value={item}>
              {capitalizeFirstLetter(item)}
            </option>
          ))}
        </Form.Select>
      </Form.Group>

      {["mobile", "email"].includes(field.validation) && (
        <MobileEmail
          field={field}
          onChangeHandler={onChangeHandler}
          fieldId={field.id}
        />
      )}
      {field.validation === "otp" && (
        <OtpOptions
          otpExpiry={
            field?.otpExpiry || {
              isExpiryEnabled: false,
              time: "",
              otpField: "",
            }
          }
          onChangeHandler={onChangeHandler}
          fieldId={field.id}
        />
      )}

      {field.validation === "pan" && (
        <PanOptions
          field={field || ""}
          onChangeHandler={onChangeHandler}
          fieldId={field.id}
        />
      )}

      {field.type === "file" && (
        <>
          <div className="mb-2">
            <div className="mb-2">File type</div>
            <Select
              defaultValue={field?.fileType || []}
              isMulti
              name="colors"
              options={selectFileTypeOptions}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={(data) => onChangeHandler(data, "fileType", field.id)}
            />
          </div>
          <InputText
            label={"Size"}
            value={field?.size}
            fieldName={"size"}
            fieldId={field.id}
            onChangeHandler={onChangeHandler}
            inputType={"number"}
            placeholder={"Enter size"}
            cols={"6"}
          />
        </>
      )}

      {["select", "multiSelect"].includes(field.type) && (
        <>
          {selectOptions.map((option, index) => (
            <>
              {" "}
              <InputText
                optionId={index}
                key={index}
                label={`Option ${index + 1}`}
                value={field?.values?.[index]}
                fieldName={"values"}
                fieldId={field.id}
                onChangeHandler={onChangeHandler}
                inputType={"string"}
                placeholder={"Enter value"}
                cols={"8"}
              />
              {selectOptions.length > 2 && (
                <Form.Group
                  name={`${index}`}
                  className="mt-4"
                  as={Col}
                  md={2}
                  onClick={(e) => deleteOption(e, index, field.id)}
                >
                  <MinusCircle
                    name={`${index}`}
                    height={"1.3rem"}
                    width={"1.3rem"}
                  />
                </Form.Group>
              )}
              <Form.Group as={Col} md={2}></Form.Group>
            </>
          ))}
          <span className="mt-3" onClick={addNewOption}>
            <PlusCircle height={"2rem"} width={"2rem"} />
          </span>
        </>
      )}

      {field.type === "date" && (
        <>
          <CustomSelect
            label={"Date type"}
            value={field?.minDateType}
            fieldName={"minDateType"}
            fieldId={field.id}
            onChangeHandler={onChangeHandler}
            options={selectDateOptions}
          />
          <CustomSelect
            label={"Date type"}
            value={field?.maxDateType}
            fieldName={"maxDateType"}
            fieldId={field.id}
            onChangeHandler={onChangeHandler}
            options={selectDateOptions}
          />
        </>
      )}

      {field.type === "date" && field?.minDateType === "datepicker" ? (
        <InputText
          label={"Min"}
          value={field?.minDateValue}
          fieldName={"minDateValue"}
          fieldId={field.id}
          onChangeHandler={onChangeHandler}
          inputType={"date"}
          placeholder={""}
          cols={"6"}
        />
      ) : (
        ["year", "month", "week", "days"].includes(field?.minDateType) && (
          <InputText
            label={"Min"}
            value={field?.minDateValue}
            fieldName={"minDateValue"}
            fieldId={field.id}
            onChangeHandler={onChangeHandler}
            inputType={"number"}
            placeholder={""}
            cols={"6"}
          />
        )
      )}

      {field.type === "date" && field?.maxDateType === "datepicker" ? (
        <InputText
          label={"Max"}
          value={field?.maxDateValue}
          fieldName={"maxDateValue"}
          fieldId={field.id}
          onChangeHandler={onChangeHandler}
          inputType={"date"}
          placeholder={""}
          cols={"6"}
        />
      ) : (
        ["year", "month", "week", "days"].includes(field?.maxDateType) && (
          <InputText
            label={"Max"}
            value={field?.maxDateValue}
            fieldName={"maxDateValue"}
            fieldId={field.id}
            onChangeHandler={onChangeHandler}
            inputType={"number"}
            placeholder={""}
            cols={"6"}
          />
        )
      )}

      <Prepopulate
        field={field}
        fieldList={workflowFieldsList}
        onChangeHandler={onChangeHandler}
        uniqueKeyList={uniqueKeyList}
        uniqueKeyListHandler={uniqueKeyListHandler}
      />
      <Form.Group className="mb-3" as={Col} md="3">
        <Form.Label>{"Field show"}</Form.Label>
        <Form.Check
          className="custom-checkbox-lg"
          checked={field.fieldShow}
          onChange={(e) => onChangeHandler(e, "fieldShow", field.id)}
        />
      </Form.Group>
    </Row>
  );
};

export default ExpandedFields;
