import React from "react";
import Select from "react-select";
import { selectPennyDropOptions } from "../../../../appConfig";
import { Accordion, Col, Form } from "react-bootstrap";

const PennyDrop = ({ pennyDropValues, setterFunc, onChangeHandler }) => {
  return (
    <Accordion.Item eventKey="4" className="mt-3 border">
      <Accordion.Header>Penny drop</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Select
            defaultValue={pennyDropValues}
            isMulti
            name="pennyDrop"
            options={selectPennyDropOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={(data) =>
              onChangeHandler(data, setterFunc, "pennyDropValues")
            }
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default PennyDrop;
