import React from "react";
import { Form, Col } from "react-bootstrap";
import { capitalizeFirstLetter } from "../../../helpers/utils";

const InputText = (props) => {
  const {
    label,
    value,
    fieldName,
    fieldId,
    onChangeHandler,
    inputType,
    placeholder,
    errorIds,
    cols,
    optionId,
    marginBottom,
    isDisabled,
  } = props;
  let mofifyLabel = label?.split("_").join(" ");
  return (
    <Form.Group
      className={marginBottom ? marginBottom : "mb-2"}
      as={Col}
      md={cols}
    >
      {label && <Form.Label>{capitalizeFirstLetter(mofifyLabel)}</Form.Label>}
      <Form.Control
        disabled={isDisabled}
        type={inputType}
        placeholder={placeholder}
        value={value}
        onChange={(e) => onChangeHandler(e, fieldName, fieldId)}
        name={optionId}
      />
      {errorIds && (
        <span
          className={`${
            errorIds.includes(fieldId) ? "d-block" : "d-none"
          } text-danger`}
        >
          Please enter field name.
        </span>
      )}
    </Form.Group>
  );
};

export default InputText;
