import React from "react";
import { Accordion, Col, Form, Row } from "react-bootstrap";

const PanOptions = ({ field, onChangeHandler, fieldId }) => {
  console.log("field from pan options", field);
  const checkOptions = [
    {
      label: "Kra check",
      value: field.kraCheck,
      fieldName: "kraCheck",
    },
    {
      label: "Income tax name check",
      value: field.incometaxNameCheck,
      fieldName: "incometaxNameCheck",
    },
    {
      label: "Aadhar seeding status",
      value: field.aadharSeedingStatus,
      fieldName: "aadharSeedingStatus",
    },
    {
      label: "Back office check",
      value: field.backofficeCheck,
      fieldName: "backofficeCheck",
    },
    {
      label: "PMLA check",
      value: field.pmlaCheck,
      fieldName: "pmlaCheck",
    },
  ];

  return (
    <>
      <Row>
        {checkOptions.map((checkOption) => (
          <Form.Group as={Col} md="12">
            <Form.Check
              label={checkOption.label}
              className="mb-3 fs-5"
              checked={checkOption?.value}
              onChange={(e) =>
                onChangeHandler(e, "panCheck", fieldId, checkOption.fieldName)
              }
            />
          </Form.Group>
        ))}
      </Row>
    </>
  );
};

export default PanOptions;
