import React from "react";
import { Col, Form, Row } from "react-bootstrap";

const CheckBoxAndInput = (props) => {
  const { inputPlaceholder, onChangeHandler, pdfOptions, workflowFieldsList } =
    props;

  return (
    <Row>
      <Form.Group className="mb-3" as={Col} md="6">
        <Form.Select
          value={pdfOptions.pdfField}
          onChange={(e) => onChangeHandler(e, "pdfField")}
        >
          <option>Select</option>
          {workflowFieldsList.length > 0 &&
            workflowFieldsList.map((option, index) => (
              <option key={index} value={option}>
                {option}
              </option>
            ))}
        </Form.Select>
      </Form.Group>
      <Form.Group as={Col} md="6">
        <Form.Control
          placeholder={inputPlaceholder}
          value={pdfOptions.pdfSuffix}
          onChange={(e) => onChangeHandler(e, "pdfSuffix")}
        />
      </Form.Group>
    </Row>
  );
};

export default CheckBoxAndInput;
