import React from "react";
import CustomSelect from "../../../../../Form/Select";
import { selectDataTypeOptions } from "../../../../../../appConfig";
import Checkbox from "../../../../../Form/Checkbox";

const TypeSection = (props) => {
  const { field, onChangeHandler } = props;
  return (
    <>
      <CustomSelect
        label={"Input Type"}
        value={field.type || "text"}
        fieldName={"type"}
        fieldId={field.id}
        onChangeHandler={onChangeHandler}
        options={selectDataTypeOptions}
        col={"5"}
      />

      <Checkbox
        label={"Mandatory"}
        value={["no", false].includes(field.mandatory) ? false : true}
        fieldName={"mandatory"}
        fieldId={field.id}
        onChangeHandler={onChangeHandler}
        col={"3"}
      />
      <Checkbox
        label={"Duplicacy Allowed"}
        value={field?.duplicacy_allowed}
        fieldName={"duplicacy_allowed"}
        fieldId={field.id}
        onChangeHandler={onChangeHandler}
        col={"4"}
      />
    </>
  );
};

export default TypeSection;
