import React from "react";
import { Accordion, Col, Form } from "react-bootstrap";

const AdminVisibility = ({ editShowAdmin, setEditShowAdmin }) => {
  const onchangeHandler = (e, field) => {
    setEditShowAdmin({ ...editShowAdmin, [field]: e.target.checked });
  };

  const checkOptions = [
    {
      label: "Show in admin",
      value: editShowAdmin.show,
      fieldName: "show",
    },
    {
      label: "Editable in admin",
      value: editShowAdmin.edit,
      fieldName: "edit",
    },
  ];

  return (
    <Accordion.Item eventKey="16" className="mt-3 border">
      <Accordion.Header>Admin Visibility</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <div className="d-flex">
            {checkOptions.map((checkOption) => (
              <Form.Check
                key={checkOption.label}
                label={checkOption.label}
                className="mb-2 fs-4 me-4"
                checked={checkOption.value}
                onChange={(e) => onchangeHandler(e, checkOption.fieldName)}
              />
            ))}
          </div>
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default AdminVisibility;
