import React from "react";
import { Accordion, Col, Form } from "react-bootstrap";

const KRA = ({ selectedKRA, setSelectedKRA }) => {
  const onChangeHandler = (e) => {
    setSelectedKRA(e.target.value);
  };

  return (
    <Accordion.Item eventKey="9" className="mt-3 border">
      <Accordion.Header>KRA</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <div className="d-flex">
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="yes"
              label="Yes"
              value="yes"
              checked={selectedKRA === "yes"}
              onChange={(e) => onChangeHandler(e)}
            />
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="no"
              label="No"
              value="no"
              checked={selectedKRA === "no"}
              onChange={(e) => onChangeHandler(e)}
            />
          </div>
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default KRA;
