import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import FormFields from "./FormFields";
import Accordion from "react-bootstrap/Accordion";
import { useLocation } from "react-router-dom";

import PlusCircle from "../../../icons/PlusCircle";
import { workflowAPI } from "../../../services/workflowApi";
import { errorToast, successToast } from "../../../helpers/toast";
import DrawerFooter from "./DrawerFooter";
import EditShowAdmin from "./EditShowAdmin";
import {
  conditionalFlowDefaultValue,
  defaultDigilockerValue,
  defaultNotificationsValues,
  financialDefaultData,
  termsConditionsDefaultValue,
} from "../../../appConfig";
import UniversalAccordions from "./UniversalAccordions";
import ConditionalAccordions from "./ConditionalAccordions";

const DrawerForm = ({
  moduleList,
  setShowDrawer,
  rawResponse,
  selectedNode,
  getData,
}) => {
  console.log("selectedNode", selectedNode);
  let location = useLocation();
  const { key: workflowKey, workflowName } = location.state || {};

  const [fields, setFields] = useState(selectedNode?.fields || []);

  const [termsConditions, setTermsConditions] = useState(
    Array.isArray(selectedNode?.termsConditions)
      ? selectedNode?.termsConditions
      : [selectedNode?.termsConditions || termsConditionsDefaultValue]
  );

  const [popupAfterSubmit, setPopupAfterSubmit] = useState(
    selectedNode?.popupAfterSubmit || { show: false, value: "" }
  );

  const [submitButton, setSubmitButton] = useState(
    selectedNode?.submitButton || { showButton: false, buttonName: "" }
  );

  const [editShowAdmin, setEditShowAdmin] = useState({
    show: selectedNode?.showAdmin,
    edit: selectedNode?.editAdmin,
    verify: selectedNode?.verify,
    pagePreview: selectedNode?.pagePreview,
  });

  const [emailValues, setEmailValues] = useState(
    selectedNode?.emailValues || []
  );

  const [digilockerValues, setDigilockerValues] = useState(
    selectedNode?.digilockerValues || defaultDigilockerValue
  );

  const [faceIPVValues, setFaceIPVValues] = useState(
    selectedNode?.faceIPVValues || null
  );

  const [signatureValues, setSignatureValues] = useState(
    selectedNode?.signatureValues || null
  );

  const [selectedKRA, setSelectedKRA] = useState(
    selectedNode?.selectedKRA || null
  );

  const [selectedProcess, setSelectedProcess] = useState(
    selectedNode?.selectedProcess || "user"
  );

  const [pennyDropValues, setPennyDropValues] = useState(
    selectedNode?.pennyDropValues || []
  );

  const [financialData, setFinancialData] = useState(
    selectedNode?.financialData || financialDefaultData
  );

  const [notifications, setNotifications] = useState(
    selectedNode?.notifications || defaultNotificationsValues
  );

  const [uniqueKeyList, setUniqueKeyList] = useState(
    rawResponse[Object.keys(rawResponse)[0]]?.unique_key || []
  );

  console.log("uniqueKeyList", uniqueKeyList);
  const [emailOtp, setEmailOtp] = useState(
    selectedNode?.otpExpiry || {
      isExpiryEnabled: false,
      time: "",
      otpLength: "",
    }
  );

  const [mobileOtp, setMobileOtp] = useState(
    selectedNode?.otpExpiry || { isExpiryEnabled: false, time: "" }
  );

  const [otpExpiry, setOtpExpiry] = useState(
    selectedNode?.otpExpiry || {
      isExpiryEnabled: false,
      time: "",
      otpLength: "",
    }
  );

  const [conditionalflow, setConditionalflow] = useState(
    Array.isArray(selectedNode?.conditionalFlow)
      ? selectedNode?.conditionalFlow
      : [selectedNode?.conditionalFlow || conditionalFlowDefaultValue]
  );

  const [workflowFieldsList, setWorkflowFieldsList] = useState([]);
  const [workflowFieldsListType, setWorkflowFieldsListType] = useState({});

  const [errorIds, setErrorIds] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [tempAccordionFielddata, setTempAccordionFielddata] = useState({});

  const [pdfOptions, setPdfOptions] = useState(
    selectedNode?.pdfOptions || {
      selectedPDF: "",
      pdfField: "",
      pdfSuffix: "",
    }
  );

  const [esignOptions, setEsignOptions] = useState(
    selectedNode?.esignOptions || {
      selectedEsign: "",
      pdfname: "",
      esignSuffix: "",
    }
  );

  const fetchFields = async () => {
    try {
      const data = await workflowAPI.fetchWorkflowFields(workflowKey);
      const fetchValues = Object.keys(data);
      setWorkflowFieldsList(fetchValues);
      setWorkflowFieldsListType(data);
    } catch (err) {
      errorToast("Error fetching fields");
    }
  };

  const uniqueKeyListHandler = (e, fieldName) => {
    const keyExist = uniqueKeyList?.some((item) => item === fieldName);

    if (keyExist) {
      const filteredKeys = uniqueKeyList.filter((item) => item !== fieldName);
      setUniqueKeyList(filteredKeys);
    } else {
      if (uniqueKeyList.length < 2) {
        setUniqueKeyList([...uniqueKeyList, fieldName]);
      }
    }
  };

  const financialDataHandler = (e, field) => {
    if (field === "fileType") {
      setFinancialData({ ...financialData, [field]: e });
    } else {
      setFinancialData({ ...financialData, [field]: e.target.value });
    }
  };

  const multiSelectHandler = (data, setterFucntion, field) => {
    setterFucntion(data);
    setTempAccordionFielddata({ ...tempAccordionFielddata, [field]: data });
  };

  const onChangeHandler = (event, key, id, action) => {
    setFields((fields) =>
      fields.map((field) =>
        field.id === id ? updateField(field, key, event, action) : field
      )
    );
  };

  const updateField = (field, key, event, action) => {
    const { target } = event;

    switch (key) {
      case "mandatory":
      case "duplicacy_allowed":
      case "isPrepopulate":
      case "fieldShow":
      case "revalidate":
        return { ...field, [key]: target.checked };
      case "panCheck":
        return { ...field, [action]: target.checked };
      case "values":
        const updatedValues = updateValues(field.values, event, action);
        return { ...field, [key]: updatedValues };
      case "fileType":
        return { ...field, [key]: event };
      case "otpExpiry":
        const otpExpiryValue =
          action === "isExpiryEnabled" ? target.checked : target.value;
        return {
          ...field,
          [key]: { ...field?.otpExpiry, [action]: otpExpiryValue },
        };
      case "mobileemail":
        const mobileEmailValue =
          action === "setOtp" ? target.checked : target.value;
        return {
          ...field,
          [action]: mobileEmailValue,
        };

      default:
        return { ...field, [key]: target.value };
    }
  };

  const updateValues = (values, event, action) => {
    const index = parseInt(event.target.getAttribute("name"));

    if (action === "optionDelete") {
      values.splice(index, 1);
      return values;
    } else {
      const newValue = event.target.value;
      const updatedValues = values
        ? [...values.slice(0, index), newValue, ...values.slice(index + 1)]
        : [newValue];

      return updatedValues;
    }
  };

  const addNewField = () => {
    const lastField = fields.length > 0 ? fields[fields.length - 1].id : 0;
    const newField = {
      id: lastField + 1,
      name: "",
      type: "text",
      mandatory: false,
      validation: "no",
      duplicacy_allowed: true,
      fieldShow: true,
      revalidate: false,
    };

    setFields([...fields, newField]);
  };

  useEffect(() => {
    const isStaticFieldExist = selectedNode?.fields?.some(
      (field) => field.isDisabled === true
    );

    if (!isStaticFieldExist) {
      try {
        workflowAPI.fetchStaticFields().then((res) => {
          const newDataArray = [];
          Object.keys(res).forEach((key) => {
            if (key === selectedNode?.data?.label) {
              Object.keys(res[key]).forEach((keys) => {
                let modifiedData = {
                  id: Math.floor(Math.random() * 900),
                  name: keys,
                  type: "text",
                  mandatory: false,
                  validation: "no",
                  duplicacy_allowed: true,
                  isDisabled: true,
                  label: keys,
                  label_type: res[key][keys],
                };
                newDataArray.push(modifiedData);
              });
            }
          });

          setFields([...fields, ...newDataArray]);
        });
      } catch (error) {
        errorToast(error.message);
      }
    }
  }, [selectedNode?.fields]);

  const fieldDeleteHandler = (id) => {
    const filteredFields = fields.filter((field) => field.id !== id);
    setFields(filteredFields);
  };

  const updateDrawerData = async () => {
    setErrorIds([]);
    const formattedRawResponse = rawResponse;
    formattedRawResponse[selectedNode.id].fields = fields;
    formattedRawResponse[selectedNode.id].showAdmin = editShowAdmin.show;
    formattedRawResponse[selectedNode.id].editAdmin = editShowAdmin.edit;
    formattedRawResponse[selectedNode.id].verify = editShowAdmin.verify;
    formattedRawResponse[selectedNode.id].pagePreview = editShowAdmin.pagePreview;
    formattedRawResponse[selectedNode.id].selectedProcess = selectedProcess;
    formattedRawResponse[selectedNode.id].termsConditions = termsConditions;
    formattedRawResponse[selectedNode.id].popupAfterSubmit = popupAfterSubmit;
    formattedRawResponse[selectedNode.id].submitButton = submitButton;
    formattedRawResponse[selectedNode.id].conditionalFlow = conditionalflow;

    for (let key in tempAccordionFielddata) {
      formattedRawResponse[selectedNode.id][key] = tempAccordionFielddata[key];
    }

    formattedRawResponse[Object.keys(formattedRawResponse)[0]].unique_key =
      uniqueKeyList;

    for (let key in formattedRawResponse) {
      let isTypeFile = formattedRawResponse[key].fields?.some(
        (field) => field.type === "file"
      );
      if (isTypeFile) {
        formattedRawResponse[key].on_post = "form";
      } else {
        formattedRawResponse[key].on_post = "json";
      }
    }

    // try {
    //   const companyName = sessionStorage.getItem("companyName");
    //   const res = await workflowAPI.updateWorkflow(
    //     formattedRawResponse,
    //     workflowKey,
    //     companyName
    //   );
    //   successToast("Fields updated successfully!");
    //   setIsLoading(false);
    //   setShowDrawer(false);
    //   setTempAccordionFielddata({});
    //   getData(workflowKey);
    // } catch (err) {
    //   errorToast("Fields wasn't updated!");
    //   setIsLoading(false);
    // }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    event.stopPropagation();
    setIsLoading(true);

    const errorIds = fields
      .filter((field) => field.name === "")
      .map((field) => field.id);

    if (errorIds.length > 0) {
      setErrorIds(errorIds);
      setIsLoading(false);
    } else {
      if (
        notifications.emailCheckboxValue &&
        !notifications.emailInputValue.trim()
      ) {
        errorToast("Please fill in the email input field");
        setIsLoading(false);
        return;
      }
      if (
        notifications.whatsappCheckboxValue &&
        !notifications.whatsappInputValue.trim()
      ) {
        errorToast("Please fill in the whatsapp input field");
        setIsLoading(false);
        return;
      }
      if (
        notifications.smsCheckboxValue &&
        !notifications.smsInputValue.trim()
      ) {
        errorToast("Please fill in the sms input field");
        setIsLoading(false);
        return;
      }
      updateDrawerData();
    }
  };

  useEffect(() => {
    fetchFields();
  }, []);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      otpExpiry: otpExpiry,
    });
  }, [otpExpiry]);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      notifications: notifications,
    });
  }, [notifications]);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      pdfOptions: pdfOptions,
    });
  }, [pdfOptions]);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      esignOptions: esignOptions,
    });
  }, [esignOptions]);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      financialData: financialData,
    });
  }, [financialData]);

  useEffect(() => {
    setTempAccordionFielddata({
      ...tempAccordionFielddata,
      selectedKRA: selectedKRA,
    });
  }, [selectedKRA]);

  return (
    <>
      <Accordion>
        <Accordion.Item eventKey="0">
          <Accordion.Header>Fields</Accordion.Header>
          <Accordion.Body>
            <Form>
              <FormFields
                fields={fields}
                onChangeHandler={onChangeHandler}
                fieldDeleteHandler={fieldDeleteHandler}
                errorIds={errorIds}
                uniqueKeyList={uniqueKeyList}
                uniqueKeyListHandler={uniqueKeyListHandler}
                workflowFieldsList={workflowFieldsList}
                setFields={setFields}
              />
              <div className="d-flex justify-content-between ">
                <Button variant="primary" onClick={addNewField}>
                  <PlusCircle />
                </Button>
              </div>
            </Form>
          </Accordion.Body>
        </Accordion.Item>

        <UniversalAccordions
          termsConditions={termsConditions}
          setTermsConditions={setTermsConditions}
          popupAfterSubmit={popupAfterSubmit}
          setPopupAfterSubmit={setPopupAfterSubmit}
          notifications={notifications}
          setNotifications={setNotifications}
          workflowFieldsList={workflowFieldsList}
          moduleList={moduleList}
          conditionalflow={conditionalflow}
          setConditionalflow={setConditionalflow}
          workflowFieldsListType={workflowFieldsListType}
          selectedProcess={selectedProcess}
          setSelectedProcess={setSelectedProcess}
          editShowAdmin={editShowAdmin}
          setEditShowAdmin={setEditShowAdmin}
          submitButton={submitButton}
          setSubmitButton={setSubmitButton}
        />

        <ConditionalAccordions
          selectedNode={selectedNode}
          setPdfOptions={setPdfOptions}
          pdfOptions={pdfOptions}
          workflowFieldsList
          setEsignOptions={setEsignOptions}
          esignOptions={esignOptions}
          pennyDropValues={pennyDropValues}
          setPennyDropValues={setPennyDropValues}
          multiSelectHandler={multiSelectHandler}
          emailValues={emailValues}
          setEmailValues={setEmailValues}
          signatureValues={signatureValues}
          setSignatureValues={setSignatureValues}
          faceIPVValues={faceIPVValues}
          setFaceIPVValues={setFaceIPVValues}
          digilockerValues={digilockerValues}
          setDigilockerValues={setDigilockerValues}
          selectedKRA={selectedKRA}
          setSelectedKRA={setSelectedKRA}
          financialData={financialData}
          financialDataHandler={financialDataHandler}
        />
        <EditShowAdmin
          editShowAdmin={editShowAdmin}
          setEditShowAdmin={setEditShowAdmin}
        />

        <DrawerFooter handleSubmit={handleSubmit} isLoading={isLoading} />
      </Accordion>
    </>
  );
};

export default DrawerForm;
