import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { capitalizeFirstLetter } from "../../../../../helpers/utils";
import { selectFieldsOptions } from "../../../../../appConfig";
import MinusCircle from "../../../../../icons/MinusCircle";
import DateTypeValue from "./DateTypeValue";

const IfSection = ({
  fields,
  item,
  onChangeHandler,
  deleteSection,
  sectionsLength,
  workflowFieldsListType,
}) => {
  return (
    <>
      <div className="d-flex justify-content-between mt-3">
        <h4 className="mb-3">If</h4>
        {sectionsLength > 1 && (
          <Button
            variant="danger"
            style={{
              height: "2.5rem",
              width: "3rem",
            }}
            onClick={() => deleteSection(item.sectionId)}
          >
            <MinusCircle marginTop={"0rem"} />
          </Button>
        )}
      </div>
      <Row>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Select
            value={item.field}
            onChange={(e) =>
              onChangeHandler(e, "field", "if", null, item.sectionId)
            }
          >
            <option>Select</option>
            {fields.map((item, index) => (
              <option key={index} value={item}>
                {capitalizeFirstLetter(item)}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Select
            value={item.operator}
            onChange={(e) =>
              onChangeHandler(e, "operator", "if", null, item.sectionId)
            }
          >
            <option>Select</option>
            {selectFieldsOptions.map((item, index) => (
              <option key={index} value={item}>
                {capitalizeFirstLetter(item)}
              </option>
            ))}
          </Form.Select>
        </Form.Group>

        {workflowFieldsListType[item.field] !== "date" && (
          <Form.Group as={Col} md="4">
            <Form.Control
              placeholder={"Enter here..."}
              value={item.inputValue}
              onChange={(e) =>
                onChangeHandler(e, "inputValue", "if", null, item.sectionId)
              }
            />
          </Form.Group>
        )}
      </Row>

      {workflowFieldsListType[item.field] === "date" && (
        <DateTypeValue item={item} onChangeHandler={onChangeHandler} />
      )}
    </>
  );
};

export default IfSection;
