import React from "react";
import Select from "react-select";
import { selectDigilockerOptions } from "../../../../appConfig";
import { Accordion, Col, Form } from "react-bootstrap";

const Digilocker = ({ digilockerValues, onChangeHandler, setterFunc }) => {
  return (
    <Accordion.Item eventKey="8" className="mt-3 border">
      <Accordion.Header>Digilocker</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Select
            defaultValue={digilockerValues}
            isMulti
            name="Digilocker"
            options={selectDigilockerOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={(data) =>
              onChangeHandler(data, setterFunc, "digilockerValues")
            }
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default Digilocker;
