import React, { useEffect, useState } from "react";
import { Accordion, Col, Form } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { workflowAPI } from "../../../../services/workflowApi";
import { errorToast } from "../../../../helpers/toast";
import CheckBoxAndInput from "./CheckBoxAndInput";

const PdfEditor = ({ setPdfOptions, pdfOptions, workflowFieldsList }) => {
  let location = useLocation();
  const { workflowName } = location.state || {};
  const companyName = sessionStorage.getItem("companyName");

  const [options, setOptions] = useState([]);

  const fetchData = async () => {
    try {
      if (companyName && workflowName) {
        const res = await workflowAPI.fetchWorkflowPdf(
          workflowName,
          companyName
        );
        setOptions(res?.pdfs);
      }
    } catch (err) {
      errorToast("Something went wrong!");
    }
  };

  const onChangeHandler = (e, key) => {
    setPdfOptions({
      ...pdfOptions,
      [key]: e.target.value,
    });
  };

  useEffect(() => {
    fetchData();
  }, [companyName, workflowName]);

  return (
    <Accordion.Item eventKey="1" className="mt-3 border">
      <Accordion.Header>PDF</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Form.Select
            value={pdfOptions.selectedPDF}
            onChange={(e) => onChangeHandler(e, "selectedPDF")}
          >
            <option>Select</option>
            {options?.length > 0 &&
              options.map((option, index) => (
                <option key={index} value={option}>
                  {option}
                </option>
              ))}
          </Form.Select>
        </Form.Group>

        <CheckBoxAndInput
          checkboxField={"emailCheckboxValue"}
          inputField={"emailInputValue"}
          inputPlaceholder={"Enter Name ..."}
          onChangeHandler={onChangeHandler}
          pdfOptions={pdfOptions}
          workflowFieldsList={workflowFieldsList}
        />
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default PdfEditor;
