import React from "react";
import { Form } from "react-bootstrap";

const EditShowAdmin = ({ editShowAdmin, setEditShowAdmin }) => {
  const onchangeHandler = (e, field) => {
    setEditShowAdmin({ ...editShowAdmin, [field]: e.target.checked });
  };

  const checkOptions = [
    // {
    //   label: "Show in admin",
    //   value: editShowAdmin.show,
    //   fieldName: "show",
    // },
    // {
    //   label: "Editable in admin",
    //   value: editShowAdmin.edit,
    //   fieldName: "edit",
    // },
    {
      label: "Verify",
      value: editShowAdmin.verify,
      fieldName: "verify",
    },
    {
      label: "Page preview",
      value: editShowAdmin.pagePreview,
      fieldName: "pagePreview",
    },
  ];

  return (
    <div className="mb-3 fs-4 mt-3 checkboxParent">
      {checkOptions.map((checkOption) => (
        <Form.Check
          key={checkOption.label}
          label={checkOption.label}
          className="mb-2"
          checked={checkOption.value}
          onChange={(e) => onchangeHandler(e, checkOption.fieldName)}
        />
      ))}
    </div>
  );
};

export default EditShowAdmin;
