import React from "react";
import Select from "react-select";
import { selectFaceIPVOptions } from "../../../../appConfig";
import { Accordion, Col, Form } from "react-bootstrap";

const FaceIPV = ({ faceIPVValues, onChangeHandler, setterFunc }) => {
  return (
    <Accordion.Item eventKey="7" className="mt-3 border">
      <Accordion.Header>Face IPV</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Select
            defaultValue={faceIPVValues}
            isMulti
            name="FaceIPV"
            options={selectFaceIPVOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={(data) =>
              onChangeHandler(data, setterFunc, "faceIPVValues")
            }
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default FaceIPV;
