import React, { useCallback, useEffect, useState } from "react";
import { useDrop } from "react-dnd";
import ReactFlow, {
  Background,
  addEdge,
  useNodesState,
  useEdgesState,
  applyNodeChanges,
  applyEdgeChanges,
  getIncomers,
  getOutgoers,
  getConnectedEdges,
} from "reactflow";

import CustomNode from "../CustomNode";
import ButtonEdge from "../ButtonEdge";
import CustomConnectionLine from "../CustomConnectionLine";
import {
  connectionLineStyle,
  defaultEdgeOptions,
  getThreadList,
  updateRawResponseId,
} from "../../helpers/utils";
import Drawer from "../Drawer";
import { errorToast, successToast } from "../../helpers/toast";
import { workflowAPI } from "../../services/workflowApi";
import { useNavigate } from "react-router-dom";
import SaveCreateBtn from "./SaveCreateBtn";

let initialNodes = [];
const initialedges = [];

const nodeTypes = {
  custom: CustomNode,
};

const edgeTypes = {
  buttonedge: ButtonEdge,
};

const Content = ({
  moduleList,
  edgesList,
  workflowOrder,
  getData,
  workflowKey,
  workflowName,
}) => {
  const [nodes, setNodes] = useNodesState(initialNodes);
  const [edges, setEdges] = useEdgesState(initialedges);
  const [showDrawer, setShowDrawer] = useState(false);
  const [selectedNode, setSelectedNode] = useState(null);
  const [rawResponse, setRawResponse] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [edgeDelete, setEdgeDelete] = useState(false);
  const [payloadData, setPayloadData] = useState([]);

  const navigate = useNavigate();
  const companyName = sessionStorage.getItem("companyName");

  const handleEdgesConnection = (edge) => {
    // Clone the existing payloadData array
    const newData = [...payloadData];

    const sourceIndex = payloadData.findIndex(
      (item) => item?.id === edge?.source
    );
    const source = nodes.find((item) => item?.id === edge?.source);
    const targetIndex = payloadData.findIndex(
      (item) => item?.id === edge?.target
    );
    const target = nodes.find((item) => item?.id === edge?.target);

    if (!edgeDelete) {
      if (edge) {
        if (payloadData.length === 0) {
          newData.push(source);
          newData.push(target);
        } else {
          // If the index is found, insert the new object after it
          if (sourceIndex !== -1) {
            newData.splice(sourceIndex + 1, 0, target);
          } else {
            // If target is found, insert the source before it
            if (targetIndex !== -1) {
              newData.splice(targetIndex, 0, source);
            } else {
              if (source) {
                newData.push(source);
              }
            }
          }
        }
      }
    }
    const uniqueArray = newData.filter(
      (obj, index, self) => index === self.findIndex((t) => t?.id === obj?.id)
    );
    // Update the state with the new data
    setPayloadData(uniqueArray);
  };

  // executes when an item is dropped
  const [{}, drop] = useDrop({
    accept: "FIELDS",
    drop: (item, monitor) => {
      handleDrop(item, monitor.getClientOffset());
    },
  });

  // executes when clicked on node(module) or edges(thread)
  const handleNodeEdgeClick = (event, data) => {
    const { classList } = event.target;
    switch (classList[0]) {
      case "deleteBtn":
        onDelete(data);
        break;
      case "customNode":
        handleDrawerOpen(data);
        break;
      case "react-flow__edge-interaction":
        handleDrawerOpen(data);
        break;
      default:
        break;
    }
  };

  // executes when user delete a node
  const onDelete = useCallback(
    (item) => {
      const filteredNds = nodes.filter((node) => node.id !== item.id);
      setNodes(filteredNds);
      updateEdges(item);
      const filteredPayloadData = payloadData.filter(
        (node) => node.id !== item.id
      );
      setPayloadData(filteredPayloadData);
    },
    [nodes]
  );

  // updates edges
  const updateEdges = useCallback((edge) => {
    setEdges((eds) =>
      eds.filter((e) => ![e.target, e.source].includes(edge.id))
    );
  }, []);

  // toggles right drawer
  const handleDrawerOpen = (node) => {
    setSelectedNode(node);
    setShowDrawer(!showDrawer);
  };

  const getId = () => {
    if (nodes.length > 0) {
      const ids = nodes.map((node) => node.id);
      return Math.max(...ids) + 1;
    } else {
      return 1;
    }
  };

  // executes when item is dropped in the drop section
  const handleDrop = (item, position) => {
    console.log("inside handle drop: ", item,position)
    const itemId = getId();
    onNodeAdd({
      position: position,
      ...item,
      id: `${itemId}`,
      data:{label:item.page_name},
      name:item.page_name,
      type:'custom',
      // position: { ...position, x: position.x, y: position.y + 320 },
      // editAdmin: true,
      // showAdmin: true,
      // verify: false,
      // pagePreview: true,
      // selectedProcess: "user",
    });
    let formattedRawResponse = rawResponse;
    formattedRawResponse[`${itemId}`] = {
      name: item.page_name,
      // name: item.data.label,
      on_post: "json",
      ...item,
    };
    setRawResponse(formattedRawResponse);
  };

  // executes when we connect one node to another
  const onConnect = useCallback(
    (params) => {
      setEdges((eds) => addEdge(params, eds));
      setEdgeDelete(false);
    },
    [setEdges]
  );

  // executes when we add a new node
  const onNodeAdd = useCallback(
    (changes) => setNodes((nds) => nds.concat(changes)),
    [nodes]
  );

  // executes when we moves a node
  const onNodesChange = useCallback(
    (changes) => setNodes((nds) => applyNodeChanges(changes, nds)),
    []
  );

  // executes when we moves edge
  const onEdgesChange = useCallback((changes) => {
    console.log("changes called");
    setEdges((eds) => applyEdgeChanges(changes, eds));
  }, []);

  // executes when save workflow changes
  const saveHandler = async () => {
    setIsLoading(true);
    const companyName = sessionStorage.getItem("companyName");
    const updatedRawResponse = updateRawResponseId(payloadData, {
      ...rawResponse,
    });
    try {
      const res = await workflowAPI.updateWorkflow(
        updatedRawResponse,
        workflowKey,
        companyName
      );

      // module arrange logic
      const data = payloadData.length > 1 ? payloadData : [];
      const saveWorkflowOrderData = {
        workflowKey: workflowKey,
        edgesList: edges,
        workflowOrder: data,
      };
      const response = await workflowAPI.saveWorkflowOrder(
        saveWorkflowOrderData
      );
      // module arrange logic end

      successToast("Workflow updated successfully!");
      setIsLoading(false);
      getData(workflowKey);
    } catch (err) {
      errorToast("Workflow wasn't updated!");
      setIsLoading(false);
    }
  };

  // executes when we create new workflow
  const createHandler = async () => {
    setIsLoading(true);
    for (let key in rawResponse) {
      rawResponse[key] = { ...rawResponse[key] };
    }
    const companyName = sessionStorage.getItem("companyName");
    const updatedRawResponse = updateRawResponseId(payloadData, {
      ...rawResponse,
    });
    try {
      const res = await workflowAPI.createWorkflow(
        updatedRawResponse,
        workflowName,
        companyName
      );

      // module arrange logic
      const data = payloadData.length > 1 ? payloadData : [];
      const saveWorkflowOrderData = {
        workflowKey: res.workflow_key,
        edgesList: edges,
        workflowOrder: data,
      };
      const response = await workflowAPI.saveWorkflowOrder(
        saveWorkflowOrderData
      );
      // module arrange logic end

      successToast("Workflow created successfully!");
      setIsLoading(false);
      navigate(`/?companyName=${btoa(companyName)}`);
    } catch (err) {
      errorToast("Workflow wasn't created!");
      setIsLoading(false);
    }
  };

  // save workflow || create workflow click handler 
  const saveCreateClickHandler = (action) => {
    switch (action) {
      case "create":
        createHandler();
        break;
      default:
        saveHandler();
        break;
    }
  };

  useEffect(() => {
    setNodes(moduleList);
    const edges = getThreadList(moduleList);
    setEdges(edges);
    setPayloadData(moduleList);
  }, [moduleList]);

  useEffect(() => {
    for (let edge of edges) {
      handleEdgesConnection(edge);
    }
  }, [edges]);

  useEffect(() => {
    let jsonData = {};
    if (nodes.length > 0) {
      for (let item of nodes) {
        jsonData[`${item.id}`] = {
          name: item.page_name,
          on_post: "json",
          ...item,
        };
      }
      setRawResponse(jsonData);
    }
    console.log('nodes are',nodes)
  }, [nodes]);

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <Drawer
        moduleList={moduleList}
        selectedNode={selectedNode}
        showDrawer={showDrawer}
        setShowDrawer={setShowDrawer}
        handleDrawerOpen={handleDrawerOpen}
        rawResponse={rawResponse}
        getData={getData}
      />
      <div ref={drop} className="contentContainer">
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          nodeTypes={nodeTypes}
          edgeTypes={edgeTypes}
          defaultEdgeOptions={defaultEdgeOptions}
          connectionLineComponent={CustomConnectionLine}
          connectionLineStyle={connectionLineStyle}
          defaultViewport={{ x: 200, y: 50, zoom: 0.5 }}
          onNodeClick={(event, node) => handleNodeEdgeClick(event, node)}
          onEdgeClick={(event, edge) => {
            setEdgeDelete(true);
            handleNodeEdgeClick(event, edge);
          }}
        >
          <Background />
        </ReactFlow>
      </div>
      <SaveCreateBtn
        isLoading={isLoading}
        workflowKey={workflowKey}
        saveCreateClickHandler={saveCreateClickHandler}
      />
    </div>
  );
};

export default Content;
