import React from "react";
import { Col, Form, Row } from "react-bootstrap";
import MinusCircle from "../../../../../icons/MinusCircle";
import PlusCircle from "../../../../../icons/PlusCircle";
import { capitalizeFirstLetter } from "../../../../../helpers/utils";

const ThenSection = (props) => {
  const {
    moduleList,
    fields,
    itemSection,
    onChangeHandler,
    setConditionalflow,
  } = props;

  const addNewOption = () => {
    const lastOption = itemSection.then[itemSection.then.length - 1];

    const newOption = {
      id: lastOption?.id + 1 || 1,
      moduleAndField: "",
      selectedoption: "",
      action: "hide",
    };

    setConditionalflow((prevState) =>
      prevState.map((item) => {
        if (item.sectionId === itemSection.sectionId) {
          return {
            ...item,
            then: [...item.then, newOption],
          };
        }
        return item;
      })
    );
  };

  const removeOption = (id) => {
    setConditionalflow((prevState) =>
      prevState.map((item) => {
        if (item.sectionId === itemSection.sectionId) {
          const updatedThen = item.then.filter((option) => option.id !== id);
          return {
            ...item,
            then: updatedThen,
          };
        }
        return item;
      })
    );
  };

  return (
    <div>
      <h4 className="mb-3">Then</h4>
      {itemSection.then.map((item, index) => {
        return (
          <Row key={item.id}>
            <Form.Group className="mb-3" as={Col} md="4">
              <Form.Select
                value={item.moduleAndField}
                onChange={(e) =>
                  onChangeHandler(
                    e,
                    "moduleAndField",
                    "then",
                    item.id,
                    itemSection.sectionId
                  )
                }
              >
                <option>Select</option>
                <option value="module">Module</option>
                <option value="field">Field</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3" as={Col} md="4">
              <Form.Select
                disabled={!["field", "module"].includes(item.moduleAndField)}
                value={item.selectedoption}
                onChange={(e) =>
                  onChangeHandler(
                    e,
                    "selectedoption",
                    "then",
                    item.id,
                    itemSection.sectionId
                  )
                }
              >
                <option>Select</option>
                {item.moduleAndField === "field"
                  ? fields.map((field) => (
                      <option value={field}>{field}</option>
                    ))
                  : moduleList.map((module) => (
                      <option value={module?.name}>
                        {capitalizeFirstLetter(module?.name)}
                      </option>
                    ))}
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3" as={Col} md="3">
              <Form.Select
                disabled={!item.selectedoption}
                value={item.action}
                onChange={(e) =>
                  onChangeHandler(
                    e,
                    "action",
                    "then",
                    item.id,
                    itemSection.sectionId
                  )
                }
              >
                <option value="hide">Hide</option>
                <option value="show">Show</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3" as={Col} md="1">
              {itemSection.then.length > 1 && (
                <span onClick={() => removeOption(item.id)}>
                  <MinusCircle
                    height={"1.3rem"}
                    width={"1.3rem"}
                    marginTop={"0.5rem"}
                  />
                </span>
              )}
            </Form.Group>
          </Row>
        );
      })}
      <span onClick={() => addNewOption()}>
        <PlusCircle height={"1.5rem"} width={"1.5rem"} style={{ margin: 0 }} />
      </span>
    </div>
  );
};

export default ThenSection;
