import React from "react";

import { Accordion, Button } from "react-bootstrap";
import MinusCircle from "../../icons/MinusCircle";
import DrawerForm from "../Drawer/DrawerForm";
function Module({
  item,
  index,
  deleteModule,
  moduleList,
  setShowDrawer,
  rawResponse,
  selectedNode,
  getData,
}) {
  // console.log("modulelist in drawer",moduleList);
  // console.log("setShowDrawer in drawer",setShowDrawer);
  // console.log("rawResponse in drawer",rawResponse);
  // console.log("selectedNode in drawer",selectedNode);
  // console.log("getData in drawer",getData);
  return (
    <div className="d-flex justify-content-start mb-4">
      <Accordion
        style={{ width: "100%" }} // Adjust the width as needed
        key={index}
        defaultActiveKey="0"
      >
        <Accordion.Item eventKey={index}>
          <Accordion.Header>{item.module_name}</Accordion.Header>
          <Accordion.Body>
            <DrawerForm
              moduleList={moduleList}
              setShowDrawer={setShowDrawer}
              rawResponse={rawResponse}
              selectedNode={selectedNode}
              getData={getData}
            />
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>

      <Button
        variant="danger"
        style={{
          height: "2.5rem",
          width: "3rem",
          marginLeft: "1rem",
          marginTop: ".4rem",
        }}
        onClick={() => deleteModule(index)}
      >
        <MinusCircle marginTop={"0rem"} />
      </Button>
    </div>
  );
}

export default Module;
