import React from "react";
import { Col, Form, Row } from "react-bootstrap";
import { capitalizeFirstLetter } from "../../../../../../helpers/utils";

const Prepopulate = (props) => {
  const {
    field,
    fieldList,
    onChangeHandler,
    uniqueKeyList,
    uniqueKeyListHandler,
  } = props;
  return (
    <>
      <Row>
        <Form.Group className="mb-3" as={Col} md="3">
          <Form.Label>{"Set unique"}</Form.Label>
          <Form.Check
            disabled={
              uniqueKeyList?.length > 2 && !uniqueKeyList.includes(field.name)
            }
            className="custom-checkbox-lg"
            checked={uniqueKeyList && uniqueKeyList.includes(field.name)}
            onChange={(e) => uniqueKeyListHandler(e, field.name)}
          />
        </Form.Group>

        <Form.Group className="mb-3" as={Col} md={"3"}>
          <Form.Label>{"Prepopulate"}</Form.Label>
          <Form.Check
            className="custom-checkbox-lg"
            checked={field?.isPrepopulate}
            onChange={(e) => onChangeHandler(e, "isPrepopulate", field.id)}
          />
        </Form.Group>

        <Form.Group className="mb-3" as={Col} md="6">
          <Form.Label>{"Data"}</Form.Label>
          <Form.Select
            disabled={!field?.isPrepopulate}
            value={field?.prepopulateValue}
            onChange={(e) => onChangeHandler(e, "prepopulateValue", field.id)}
          >
            <option>Select</option>
            {fieldList?.map((item, index) => (
              <option key={index} value={item}>
                {capitalizeFirstLetter(item)}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
      </Row>
    </>
  );
};

export default Prepopulate;
