import React from "react";
import { Col, Form } from "react-bootstrap";

const FormFieldsFooter = (props) => {
  const { bgColor, field, onClickHandler, children } = props;

  return (
    <Form.Group as={Col} md="2">
      <div
        className={`text-white rounded text-center ${bgColor}`}
        style={{
          fontSize: "1.2rem",
          cursor: "pointer",
        }}
        onClick={() => onClickHandler(field.id)}
      >
        {children}
      </div>
    </Form.Group>
  );
};

export default FormFieldsFooter;
