import React from "react";
import InputText from "../../../../Form/InputText";
import FormFieldsFooter from "../FormFieldFooter";
import Edit from "../../../../../icons/Edit";
import { Button, Col, Form } from "react-bootstrap";
import DownArrow from "../../../../../icons/DownArrow";
import UpArrow from "../../../../../icons/UpArrow";

const FieldEditDelete = (props) => {
  const {
    field,
    onChangeHandler,
    errorIds,
    editHandler,
    fieldDeleteHandler,
    fields,
    setFields,
    index,
    uniqueKeyList,
  } = props;

  function shiftField(clickedIndex, direction) {
    const fieldsArray = [...fields];
    const newIndex = clickedIndex + (direction === "down" ? 1 : -1);
    if (newIndex >= 0 && newIndex < fieldsArray.length) {
      [fieldsArray[clickedIndex], fieldsArray[newIndex]] = [
        fieldsArray[newIndex],
        fieldsArray[clickedIndex],
      ];
      setFields(fieldsArray);
    }
  }

  return (
    <>
      <InputText
        label={field?.label}
        value={field.name}
        fieldName={"name"}
        fieldId={field.id}
        onChangeHandler={onChangeHandler}
        inputType={"text"}
        placeholder={"Enter name"}
        errorIds={errorIds}
        cols={"6"}
        marginBottom={"0rem"}
        isDisabled={uniqueKeyList.includes(field.name)}
      />

      <>
        <FormFieldsFooter
          bgColor={"bg-primary"}
          field={field}
          onClickHandler={editHandler}
        >
          <Edit />
        </FormFieldsFooter>
        {!field?.isDisabled && (
          <FormFieldsFooter
            bgColor={"bg-danger"}
            field={field}
            onClickHandler={fieldDeleteHandler}
          >
            X
          </FormFieldsFooter>
        )}
        <Form.Group as={Col} md="2">
          <div className="d-flex justify-content-between">
            <div onClick={() => shiftField(index, "down")}>
              <DownArrow />
            </div>
            <div onClick={() => shiftField(index, "up")}>
              <UpArrow />
            </div>
          </div>
        </Form.Group>
      </>
    </>
  );
};

export default FieldEditDelete;
