import React, { useEffect, useState } from "react";

import Sidebar from "../../Components/Sidebar";
import Content from "../../Components/Content";

import { getModuleList } from "../../helpers/utils";
import { workflowAPI } from "../../services/workflowApi";
import { useLocation } from "react-router-dom";

const Workflow = () => {
  const [moduleList, setModuleList] = useState([]);
  const [pageList, setPageList] = useState([]);
  const [workflowOrderData, setWorkflowOrderData] = useState({});
  let location = useLocation();
  const { key: workflowKey, workflowName } = location.state || {};

  const getData = async (id) => {
    const companyName = sessionStorage.getItem("companyName");
    try {
      if (companyName) {
        const data = await workflowAPI.fetchWorkflow(id, companyName);
        const list = await getModuleList(data);
        const list_page = await workflowAPI.getGlobalPages()
        setPageList(list_page)
        setModuleList(list);
        const workflowOrderRes = await workflowAPI.getWorkflowOrder(id);
        setWorkflowOrderData(workflowOrderRes);
      }
    } catch (err) {
      console.log("err", err);
    }
  };

  useEffect(() => {
    if (workflowKey !== "create") {
      getData(workflowKey);
    }
  }, []);

  return (
    <div className="parentContainer">
      <Sidebar pageList={pageList} moduleList={moduleList} workflowName={workflowName} />
      <Content
        moduleList={moduleList}
        edgesList={workflowOrderData?.edgesList || []}
        workflowOrder={workflowOrderData?.workflowOrder || []}
        getData={getData}
        workflowKey={workflowKey}
        workflowName={workflowName}
      />
    </div>
  );
};

export default Workflow;
