import React from "react";
import TermsConditions from "../TermsConditions";
import PopupAfterSubmit from "../PopupAfterSubmit";
import Notifications from "../Notifications";
import ConditionalFlow from "../ConditionalFlow";
import Process from "../Process";
import AdminVisibility from "../Adminvisiblity";
import SubmitButton from "../SubmitButton";

const UniversalAccordions = (props) => {
  const {
    termsConditions,
    setTermsConditions,
    popupAfterSubmit,
    setPopupAfterSubmit,
    notifications,
    setNotifications,
    workflowFieldsList,
    moduleList,
    conditionalflow,
    setConditionalflow,
    workflowFieldsListType,
    selectedProcess,
    setSelectedProcess,
    editShowAdmin,
    setEditShowAdmin,
    submitButton,
    setSubmitButton,
  } = props;

  return (
    <>
      <TermsConditions
        termsConditions={termsConditions}
        setTermsConditions={setTermsConditions}
      />

      <PopupAfterSubmit
        popupAfterSubmit={popupAfterSubmit}
        setPopupAfterSubmit={setPopupAfterSubmit}
      />
      <Notifications
        notifications={notifications}
        setNotifications={setNotifications}
        workflowFieldsList={workflowFieldsList}
      />
      <ConditionalFlow
        moduleList={moduleList}
        conditionalflow={conditionalflow}
        setConditionalflow={setConditionalflow}
        workflowFieldsList={workflowFieldsList}
        workflowFieldsListType={workflowFieldsListType}
      />
      <Process
        selectedProcess={selectedProcess}
        setSelectedProcess={setSelectedProcess}
      />
      <AdminVisibility
        editShowAdmin={editShowAdmin}
        setEditShowAdmin={setEditShowAdmin}
      />
      <SubmitButton
        submitButton={submitButton}
        setSubmitButton={setSubmitButton}
      />
    </>
  );
};

export default UniversalAccordions;
