import React from "react";
import { Accordion, Col, Form } from "react-bootstrap";

const PopupAfterSubmit = ({ popupAfterSubmit, setPopupAfterSubmit }) => {
  const onChangeHandler = (e, key) => {
    setPopupAfterSubmit({
      ...popupAfterSubmit,
      [key]: key === "show" ? e.target.checked : e.target.value,
    });
  };

  return (
    <Accordion.Item eventKey="11" className="mt-3 border">
      <Accordion.Header>Popup After Submit</Accordion.Header>
      <Accordion.Body>
        <Form.Group as={Col} md="12">
          <Form.Check
            label={`Show`}
            className="mb-3 fs-4"
            checked={popupAfterSubmit?.show}
            onChange={(e) => onChangeHandler(e, "show")}
          />
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="12">
          <Form.Control
            disabled={!popupAfterSubmit?.show}
            value={popupAfterSubmit?.value}
            onChange={(e) => onChangeHandler(e, "value")}
            as="textarea"
            rows={5}
            placeholder="Enter HTML..."
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default PopupAfterSubmit;
