import React from "react";
import PdfEditor from "../PdfEditor";
import Esign from "../Esign";
import PennyDrop from "../PennyDrop";
import Email from "../Email";
import Signature from "../Signature";
import FaceIPV from "../FaceIPV";
import Digilocker from "../Digilocker";
import KRA from "../KRA";
import FinancialProof from "../FinancialProof";

const ConditionalAccordions = (props) => {
  const {
    selectedNode,
    setPdfOptions,
    pdfOptions,
    workflowFieldsList,
    setEsignOptions,
    esignOptions,
    pennyDropValues,
    setPennyDropValues,
    multiSelectHandler,
    emailValues,
    setEmailValues,
    signatureValues,
    setSignatureValues,
    faceIPVValues,
    setFaceIPVValues,
    digilockerValues,
    setDigilockerValues,
    selectedKRA,
    setSelectedKRA,
    financialData,
    financialDataHandler,
  } = props;

  const conditionalAccordion = {
    pdf: (
      <PdfEditor
        setPdfOptions={setPdfOptions}
        pdfOptions={pdfOptions}
        workflowFieldsList={workflowFieldsList}
      />
    ),
    esign: (
      <Esign setEsignOptions={setEsignOptions} esignOptions={esignOptions} />
    ),
    bank: (
      <PennyDrop
        pennyDropValues={pennyDropValues}
        setterFunc={setPennyDropValues}
        onChangeHandler={multiSelectHandler}
      />
    ),
    email: (
      <Email
        emailValues={emailValues}
        setterFunc={setEmailValues}
        onChangeHandler={multiSelectHandler}
      />
    ),
    signature: (
      <Signature
        signatureValues={signatureValues}
        setterFunc={setSignatureValues}
        onChangeHandler={multiSelectHandler}
      />
    ),
    face_ipv: (
      <FaceIPV
        faceIPVValues={faceIPVValues}
        setterFunc={setFaceIPVValues}
        onChangeHandler={multiSelectHandler}
      />
    ),
    digilocker: (
      <Digilocker
        digilockerValues={digilockerValues}
        setterFunc={setDigilockerValues}
        onChangeHandler={multiSelectHandler}
      />
    ),
    kra: <KRA selectedKRA={selectedKRA} setSelectedKRA={setSelectedKRA} />,
    financialproof: (
      <FinancialProof
        financialData={financialData}
        onChangeHandler={financialDataHandler}
      />
    ),
    // email_otp: (
    //   <EmailOtp
    //     emailOtp={emailOtp}
    //     onChangeHandler={termsPopupAfterHandler}
    //     setterFunc={setEmailOtp}
    //   />
    // ),
    // mobile_otp: (
    //   <MobileOtp
    //     mobileOtp={mobileOtp}
    //     onChangeHandler={termsPopupAfterHandler}
    //     setterFunc={setMobileOtp}
    //   />
    // ),
  };
  return conditionalAccordion[selectedNode?.data?.label];
};

export default ConditionalAccordions;
