import React, { useState } from "react";
import Row from "react-bootstrap/Row";

import ExpandedFields from "./ExpandedFields";
import FieldEditDelete from "./FieldEditDelete";

const FormFields = ({
  fields,
  onChangeHandler,
  fieldDeleteHandler,
  errorIds,
  uniqueKeyList,
  uniqueKeyListHandler,
  workflowFieldsList,
  setFields,
}) => {
  console.log("fields", fields)
  const [selectedEditId, setSelectedEditId] = useState(null);

  const editHandler = (id) => {
    if (selectedEditId === id) {
      setSelectedEditId(null);
    } else {
      setSelectedEditId(id);
    }
  };

  return (
    <>
      {fields?.map((field, index) => (
        <Row
          key={field.id}
          className={`mb-4 border py-2 rounded align-items-center ${
            selectedEditId === field.id
              ? "border-primary border-2 shadow-sm"
              : ""
          }`}
        >
          <FieldEditDelete
            field={field}
            onChangeHandler={onChangeHandler}
            errorIds={errorIds}
            editHandler={editHandler}
            fieldDeleteHandler={fieldDeleteHandler}
            fields={fields}
            setFields={setFields}
            index={index}
            uniqueKeyList={uniqueKeyList}
          />

          <div
            className={`expanding-content ${
              field.id === selectedEditId ? "active" : ""
            }`}
          >
            <ExpandedFields
              field={field}
              onChangeHandler={onChangeHandler}
              uniqueKeyList={uniqueKeyList}
              uniqueKeyListHandler={uniqueKeyListHandler}
              workflowFieldsList={workflowFieldsList}
              fields={fields}
            />
          </div>
        </Row>
      ))}
    </>
  );
};

export default FormFields;
