import React from "react";
import Select from "react-select";
import { selectSignatureOptions } from "../../../../appConfig";
import { Accordion, Col, Form } from "react-bootstrap";

const Signature = ({ signatureValues, onChangeHandler, setterFunc }) => {
  return (
    <Accordion.Item eventKey="6" className="mt-3 border">
      <Accordion.Header>Signature</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <Select
            defaultValue={signatureValues}
            isMulti
            name="Signature"
            options={selectSignatureOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={(data) =>
              onChangeHandler(data, setterFunc, "signatureValues")
            }
          />
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default Signature;
