import React, { useEffect, useState } from "react";
import { Col, Form, Row } from "react-bootstrap";
import { workflowAPI } from "../../../../../services/workflowApi";
import { useLocation } from "react-router-dom";
import { errorToast } from "../../../../../helpers/toast";

const CheckBoxAndInput = (props) => {
  const { inputPlaceholder, onChangeHandler, esignOptions } = props;

  const [options, setOptions] = useState([]);
  let location = useLocation();
  const { workflowName } = location.state || {};
  const companyName = sessionStorage.getItem("companyName");

  const fetchEsignOptions = async () => {
    try {
      if (companyName && workflowName) {
        const res = await workflowAPI.fetchEsignOptions(
          workflowName,
          companyName
        );
        setOptions(res?.data);
      }
    } catch (err) {
      errorToast("Something went wrong!");
    }
  };

  useEffect(() => {
    fetchEsignOptions();
  }, [companyName, workflowName]);

  return (
    <Row>
      <Form.Group className="mb-3" as={Col} md="6">
        <Form.Select
          value={esignOptions.pdfname}
          onChange={(e) => onChangeHandler(e, "pdfname")}
        >
          <option>Select</option>
          {options?.length > 0 &&
            options.map((option, index) => (
              <option key={index} value={option}>
                {option}
              </option>
            ))}
        </Form.Select>
      </Form.Group>
      <Form.Group as={Col} md="6">
        <Form.Control
          placeholder={inputPlaceholder}
          value={esignOptions.esignSuffix}
          onChange={(e) => onChangeHandler(e, "esignSuffix")}
        />
      </Form.Group>
    </Row>
  );
};

export default CheckBoxAndInput;
