import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";

import HomeTable from "../../Components/HomeTable";
import { useNavigate } from "react-router-dom";
import Inputmodal from "../../Components/Modal/Inputmodal";
import { workflowAPI } from "../../services/workflowApi";
import { getUrlQueryParams } from "../../helpers/utils";
import axios from "axios";
import { errorToast } from "../../helpers/toast";

const Home = () => {
  const [workflowList, setWorkflowList] = useState([]);
  const navigate = useNavigate();
  const queryParams = getUrlQueryParams();
  const companyName = atob(queryParams?.companyName);

  const workflowNameHandler = async (
    value,
    setValue,
    selectedOption,
    selectedId
  ) => {
    const isWorkflownameExist = workflowList?.some(
      (workflow) => workflow.workflowName === value
    );
    if (!isWorkflownameExist) {
      if (selectedOption !== "select") {
        const res = await axios.post(
          `https://kycuat.meon.co.in/create_workflow/${companyName}`,
          {
            workflow: {},
            workflowName: value,
            existing_workflow_id: selectedId,
          }
        );
        setValue("");
        getWorkflowList();
      } else {
        setValue("");
        navigate("/workflow/create", {
          state: { key: "create", workflowName: value },
        });
      }
    } else {
      errorToast(
        "Workflow name must be different from existing workflow names."
      );
    }
  };

  const getWorkflowList = async () => {
    sessionStorage.setItem("companyName", companyName);
    try {
      if (companyName) {
        const data = await workflowAPI.fetchWorkflowList(companyName);
        const companyWorkflowName = Object.keys(data)[0];
        setWorkflowList(data[companyWorkflowName]);
      }
    } catch (err) {
      console.log("err", err);
    }
  };

  useEffect(() => {
    getWorkflowList();
  }, []);

  return (
    <div className="container mx-auto">
      <div className="text-end mt-2">
        <Button
          variant="primary"
          className="mb-2"
          data-bs-toggle="modal"
          data-bs-target="#inputModal"
        >
          Create Workflow
        </Button>
      </div>
      <HomeTable workflowList={workflowList} companyName={companyName} />

      <Inputmodal
        showSelect={true}
        title={"Enter Workflow Name"}
        addNewHandler={workflowNameHandler}
      />
    </div>
  );
};

export default Home;
