export const getModuleList = async (data) => {
  let moduleList = [];

  for (let key in data) {
    // const position = getPosition(key);
    let newnode = {
      // position: position,
      ...data[key],
      id: key,
      type: "custom",
      data: { label: data[key]?.name },
    };
    moduleList.push(newnode);
  }

  return moduleList;
};

export const capitalizeFirstLetter = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

export const getUrlQueryParams = () => {
  const urlParams = new URLSearchParams(window.location.search);
  // Get the value of a specific parameter
  const productName = urlParams.get("product");
  const companyName = urlParams.get("companyName");

  return {
    productName,
    companyName,
  };
};

export const updateRawResponseId = (payloadData, rawResponse) => {
  const newObj = {};

  // Loop through each item in arr
  payloadData.forEach((item, index) => {
    // Update the key of the nested object in obj based on the index
    const newKey = `${index + 1}`;
    const nestedObj = rawResponse[item.id];
    nestedObj.id = newKey; // Update the id of the nested object
    newObj[newKey] = nestedObj; // Assign the nested object to newObj with the new key
  });
  return newObj;
};

export const getThreadList = (moduleList) => {
  const threadList = [];

  for (let i = 0; i < moduleList.length - 1; i++) {
    let newThread = {
      id: `reactflow__edge-${moduleList[i]?.id}-${moduleList[i + 1]?.id}`,

      source: moduleList[i]?.id,

      target: moduleList[i + 1].id,
    };

    threadList.push(newThread);
  }

  return threadList;
};
