import React from "react";
import { Accordion, Col, Form, Row } from "react-bootstrap";
import CheckBoxAndInput from "./CheckBoxAndInput";

const Notifications = ({
  notifications,
  setNotifications,
  workflowFieldsList,
}) => {
  const notificationsHandler = (e, key) => {
    setNotifications({
      ...notifications,
      [key]: key.includes("Checkbox") ? e.target.checked : e.target.value,
    });
  };

  return (
    <Accordion.Item eventKey="12" className="mt-3 border">
      <Accordion.Header>Notifications</Accordion.Header>
      <Accordion.Body>
        <CheckBoxAndInput
          notifications={notifications}
          checkboxField={"emailCheckboxValue"}
          inputField={"emailInputValue"}
          selectField={"emailField"}
          checkLabel={"Email"}
          inputPlaceholder={"Enter id..."}
          onChangeHandler={notificationsHandler}
          workflowFieldsList={workflowFieldsList}
        />
        <CheckBoxAndInput
          notifications={notifications}
          checkboxField={"whatsappCheckboxValue"}
          inputField={"whatsappInputValue"}
          selectField={"whatsappField"}
          checkLabel={"Whatsapp"}
          inputPlaceholder={"Enter id..."}
          onChangeHandler={notificationsHandler}
          workflowFieldsList={workflowFieldsList}
        />
        <CheckBoxAndInput
          notifications={notifications}
          checkboxField={"smsCheckboxValue"}
          inputField={"smsInputValue"}
          selectField={"smsField"}
          checkLabel={"SMS"}
          inputPlaceholder={"Enter id..."}
          onChangeHandler={notificationsHandler}
          workflowFieldsList={workflowFieldsList}
        />
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default Notifications;
