import Offcanvas from "react-bootstrap/Offcanvas";
import { getDrawerText } from "../../helpers/utils";
// import DrawerForm from "./DrawerForm";
import { Form } from "react-bootstrap";
// import PlusCircle from "../../icons/PlusCircle";
import { useEffect, useState } from "react";
import { capitalizeFirstLetter } from "../../helpers/utils";

// testing purpose
// import { selectDataTypeOptions } from "../../appConfig";
//
import Module from "../Module";
// import { json } from "react-router-dom";

function Drawer({
  moduleList,
  selectedNode,
  showDrawer,
  setShowDrawer,
  handleDrawerOpen,
  rawResponse,
  getData,
}) {
  const [add, setAdd] = useState(false);
  const [selectedModule, setSelectedModule] = useState("");
  const [moduleCreated, setModuleCreated] = useState([]);
  const [pageModules, setPageModules] = useState([]);

  function showModuleDropDown() {
    setAdd(true);
  }
  function addModule(e) {
    // console.log('event is: ',JSON.parse(e.target.value))
    const itemObj = JSON.parse(e.target.value);
    if (itemObj.module_name !== "Add Module") {
      setModuleCreated([...moduleCreated, itemObj]);
      setSelectedModule("Add Module");
    }
  }
  function deleteModule(i) {
    const updatedModules = [...moduleCreated];

    updatedModules.splice(i, 1);

    setModuleCreated(updatedModules);
  }
  useEffect(() => {
    console.log("module created length is: ", moduleCreated.length);

    if (moduleCreated.length === 0) {
      setPageModules(selectedNode?.global_module);
    } else {
      // const data = selectedNode?.global_module.filter((item) => {
      //   return moduleCreated.filter((innerItem) => item.id !== innerItem.id);
      // });
      const data = selectedNode?.global_module.filter((item) => {
        // Filter out items from selectedNode.global_module where there's no matching id in moduleCreated
        return !moduleCreated.some((innerItem) => innerItem.id === item.id);
      });
      console.log("filtered data is: ", data);
      setPageModules(data);
    }
    console.log("pagemodules are:", pageModules);
  }, [selectedNode, moduleCreated]);
  useEffect(() => {
    setModuleCreated([]);
  }, []);
  return (
    <>
      <Offcanvas
        show={showDrawer}
        onHide={handleDrawerOpen}
        placement={"end"}
        style={{ width: "40%" }}
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>
            {showDrawer && getDrawerText(selectedNode)}
          </Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <div style={{ display: "flex" }}>
            <Form.Group className="mb-3 ms-3" md="4">
              <Form.Select
                value={selectedModule}
                onChange={(e) => addModule(e)}
              >
                <option value="Add Module">Add Module</option>
                {pageModules?.map((item, index) => (
                  <option key={index} value={JSON.stringify(item)}>
                    {capitalizeFirstLetter(item.module_name)}
                  </option>
                ))}
              </Form.Select>
            </Form.Group>
          </div>
          <div className="showModules">
            <hr />
            {moduleCreated?.map((item, index) => {
              return (
                <Module
                  item={item}
                  index={index}
                  deleteModule={deleteModule}
                  
                  // Props for drower form
                  moduleList={moduleList}
                  setShowDrawer={setShowDrawer}
                  rawResponse={rawResponse}
                  selectedNode={selectedNode}
                  getData={getData}
                />
              );
            })}
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
}

export default Drawer;
