import React from "react";
import { useDrag } from "react-dnd";
import { RiDraggable } from "react-icons/ri";
const DraggableTextField = ({ styling, fieldName, item }) => {
  const [, ref] = useDrag({
    type: "FIELDS",
    item: item,
  });

  return (
    <div ref={ref}  className="textContainer d-flex align-items-center" style={{border:'1px solid gray',borderRadius:'4px'}}>
      <RiDraggable  />
      <div  className="customText">{fieldName.charAt(0).toUpperCase() + fieldName.slice(1).replaceAll("_", " ")}</div>
    </div>
  );
};

export default DraggableTextField;
