import React from "react";
import { Accordion } from "react-bootstrap";
import IfSection from "./IfSection";
import ThenSection from "./ThenSection";
import { Button } from "react-bootstrap";
import PlusCircle from "../../../../icons/PlusCircle";

const ConditionalFlow = ({
  moduleList,
  conditionalflow,
  setConditionalflow,
  workflowFieldsList,
  workflowFieldsListType,
}) => {
  const addNewSection = () => {
    const lastItem =
      conditionalflow.length > 0
        ? conditionalflow[conditionalflow.length - 1].sectionId
        : 0;

    const newSection = {
      sectionId: (lastItem !== undefined ? lastItem : 0) + 1,
      field: "",
      operator: "",
      inputValue: "",
      then: [
        {
          id: 1,
          moduleAndField: "",
          selectedoption: "",
          action: "hide",
        },
      ],
    };

    setConditionalflow([...conditionalflow, newSection]);
  };

  const deleteSection = (id) => {
    const filteredSections = conditionalflow.filter(
      (item) => item.sectionId !== id
    );
    setConditionalflow(filteredSections);
  };

  const conditionalFlowHandler = (e, field, section, id, sectionId) => {
    if (section === "then") {
      setConditionalflow((items) =>
        items.map((item) => {
          if (item.sectionId === sectionId) {
            return {
              ...item,
              then: item.then.map((item2) =>
                item2.id === id
                  ? {
                      ...item2,
                      [field]: e.target.value,
                    }
                  : item2
              ),
            };
          }
          return item;
        })
      );
    } else if (section === "if") {
      setConditionalflow((items) =>
        items.map((item) =>
          item.sectionId === sectionId || sectionId === undefined
            ? {
                ...item,
                sectionId: sectionId === undefined ? 1 : item.sectionId,
                [field]: e.target.value,
              }
            : item
        )
      );
    }
  };

  return (
    <Accordion.Item eventKey="14" className="mt-3 border">
      <Accordion.Header>Conditional Flow</Accordion.Header>
      <Accordion.Body>
        {conditionalflow?.map((item) => (
          <>
            <IfSection
              fields={workflowFieldsList}
              item={item}
              onChangeHandler={conditionalFlowHandler}
              deleteSection={deleteSection}
              sectionsLength={conditionalflow.length}
              workflowFieldsListType={workflowFieldsListType}
            />
            <ThenSection
              moduleList={moduleList}
              fields={workflowFieldsList}
              itemSection={item}
              setConditionalflow={setConditionalflow}
              onChangeHandler={conditionalFlowHandler}
            />
          </>
        ))}
        <div className="text-end">
          <Button variant="primary" onClick={addNewSection}>
            <PlusCircle />
          </Button>
        </div>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default ConditionalFlow;
