import React from "react";
import { Accordion, Col, Form, Row } from "react-bootstrap";

const SubmitButton = ({ submitButton, setSubmitButton }) => {
  const onChangeHandler = (e, field) => {
    setSubmitButton({
      ...submitButton,
      [field]: field === "showButton" ? e.target.checked : e.target.value,
    });
  };

  return (
    <Accordion.Item eventKey="17" className="mt-3 border">
      <Accordion.Header>Submit Button</Accordion.Header>
      <Accordion.Body>
        <Row>
          <Form.Group as={Col} md="4">
            <Form.Label className="fs-5">Show button</Form.Label>
            <Form.Check
              className="mb-3 fs-3"
              checked={submitButton?.showButton}
              onChange={(e) => onChangeHandler(e, "showButton")}
            />
          </Form.Group>
          <Form.Group className="mb-3" as={Col} md="6">
            <Form.Label className="fs-5">Button name</Form.Label>
            <Form.Control
              disabled={!submitButton?.showButton}
              value={submitButton?.buttonName}
              onChange={(e) => onChangeHandler(e, "buttonName")}
              placeholder="Enter button name..."
            />
          </Form.Group>
        </Row>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default SubmitButton;
