import React, { useState } from "react";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import { useNavigate } from "react-router-dom";
import { apiEndpoint } from "../../appConfig";
import ConfirmationModal from "../Modal/ConfirmationModal";

const HomeTable = ({ workflowList, companyName }) => {
  const [deleteWorkflowId, setDeleteWorkflowId] = useState(null);

  const navigate = useNavigate();

  const editHandler = (workflowId, workflowName) => {
    navigate(`/workflow/${workflowId}`, {
      state: {
        key: workflowId,
        workflowName: workflowName,
      },
    });
  };

  const testWorkflowHandler = (workflowName) => {
    const url = `${apiEndpoint.baseUri}/${companyName}/${workflowName}`;
    window.open(url, "_blank");
  };

  const deleteHandler = () => {
    console.log("deleteHandler called", deleteWorkflowId);
  };

  return (
    <>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Workflow ID</th>
            <th>Workflow Name</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {workflowList.length > 0 &&
            workflowList.map((workflow) => (
              <tr key={workflow.work_flow_key}>
                <td>{workflow.work_flow_key}</td>
                <td>{workflow.workflowName}</td>
                <td className="text-success">{workflow.status}</td>
                <td>
                  <Button
                    variant="primary"
                    className="me-3 px-4"
                    onClick={() =>
                      editHandler(workflow.work_flow_key, workflow.workflowName)
                    }
                  >
                    Edit
                  </Button>
                  <Button
                    variant="success"
                    className="me-3 px-4"
                    onClick={() => testWorkflowHandler(workflow.workflowName)}
                  >
                    Test workflow
                  </Button>
                  <Button
                    variant="danger"
                    data-bs-toggle="modal"
                    data-bs-target="#deleteModal"
                    onClick={() => setDeleteWorkflowId(workflow.work_flow_key)}
                  >
                    Delete
                  </Button>
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
      <ConfirmationModal
        title={"Delete Confirmation"}
        message={"Are you sure, you want to delete this workflow?"}
        deleteHandler={deleteHandler}
      />
    </>
  );
};

export default HomeTable;
