import React from "react";
import { Accordion, Col, Form, Row } from "react-bootstrap";
import { selectFileTypeOptions } from "../../../../appConfig";
import Select from "react-select";

const FinancialProof = ({ financialData, onChangeHandler }) => {
  return (
    <Accordion.Item eventKey="10" className="mt-3 border">
      <Accordion.Header>Financial Proof</Accordion.Header>
      <Accordion.Body>
        <Row>
          <Form.Group className="mb-3" as={Col} md="6">
            <Form.Select disabled>
              <option value={"file"}>File</option>
            </Form.Select>
          </Form.Group>

          <Form.Group className="mb-3" as={Col} md="6">
            <Form.Control
              type="number"
              value={financialData.size}
              onChange={(e) => onChangeHandler(e, "size")}
            />
          </Form.Group>

          <Form.Group className="mb-3" as={Col} md="12">
            <Select
              defaultValue={financialData.fileType}
              isMulti
              name="Email"
              options={selectFileTypeOptions}
              className="basic-multi-select"
              classNamePrefix="select"
              onChange={(data) => onChangeHandler(data, "fileType")}
            />
          </Form.Group>

          <div className="d-flex mt-2 mb-2">
            <div className="fs-5 me-4">AA API: </div>
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="yes"
              label="Yes"
              value="yes"
              checked={financialData.aaAPI === "yes"}
              onChange={(e) => onChangeHandler(e, "aaAPI")}
            />
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="no"
              label="No"
              value="no"
              checked={financialData.aaAPI === "no"}
              onChange={(e) => onChangeHandler(e, "aaAPI")}
            />
          </div>
        </Row>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default FinancialProof;
