import React from "react";
import { Button } from "react-bootstrap";

const SaveCreateBtn = (props) => {
  const { isLoading, workflowKey, saveCreateClickHandler } = props;
  return (
    <div className="text-end">
      <Button
        disabled={isLoading}
        variant="primary"
        className="me-4 px-4 fs-5"
        onClick={() => saveCreateClickHandler(workflowKey)}
      >
        {workflowKey === "create" ? "Create" : "Save"}
        {isLoading && (
          <div
            className="spinner-border spinner-border-sm text-light ms-2"
            role="status"
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        )}
      </Button>
    </div>
  );
};

export default SaveCreateBtn;
