import React from "react";
import { Col, Form, Row } from "react-bootstrap";

const MobileEmail = ({ field, onChangeHandler, fieldId }) => {
  return (
    <>
      <Row>
        <Form.Group as={Col} md="4">
          <Form.Label className="fs-6">Set OTP</Form.Label>
          <Form.Check
            className="mb-3 fs-2"
            checked={field?.setOtp}
            onChange={(e) =>
              onChangeHandler(e, "mobileemail", fieldId, "setOtp")
            }
          />
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Label className=" fs-6">OTP Field</Form.Label>
          <Form.Control
            // disabled={!otpExpiry?.isExpiryEnabled}
            value={field?.otpField}
            onChange={(e) =>
              onChangeHandler(e, "mobileemail", fieldId, "otpField")
            }
            placeholder="OTP Field..."
          />
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Label className=" fs-6">OTP Length</Form.Label>

          <Form.Control
            // disabled={!otpExpiry?.isExpiryEnabled}

            type="number"
            value={field?.otpLength}
            onChange={(e) =>
              onChangeHandler(e, "mobileemail", fieldId, "otpLength")
            }
            placeholder="OTP Length"
          />
        </Form.Group>
      </Row>
    </>
  );
};

export default MobileEmail;
