import React from "react";
import { Accordion, Col, Form } from "react-bootstrap";

const Process = ({ selectedProcess, setSelectedProcess }) => {
  const onChangeHandler = (e) => {
    setSelectedProcess(e.target.value);
  };

  return (
    <Accordion.Item eventKey="15" className="mt-3 border">
      <Accordion.Header>Process</Accordion.Header>
      <Accordion.Body>
        <Form.Group className="mb-3" as={Col} md="12">
          <div className="d-flex">
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="admin"
              label="Admin"
              value="admin"
              checked={selectedProcess === "admin"}
              onChange={(e) => onChangeHandler(e)}
            />
            <Form.Check
              className="me-4 fs-5"
              type="radio"
              id="user"
              label="User"
              value="user"
              checked={selectedProcess === "user"}
              onChange={(e) => onChangeHandler(e)}
            />
          </div>
        </Form.Group>
      </Accordion.Body>
    </Accordion.Item>
  );
};

export default Process;
