import axios from "axios";
import { apiEndpoint } from "../appConfig";

export const workflowAPI = {
  getGlobalPages: async function () {
    // console.log(apiEndpoint)
    const response = await axios.get(
      `${apiEndpoint.baseUriRekyc}${apiEndpoint.getGlobalPage}`
    );

    return response.data;
  },
  // fetch workflow list
  fetchWorkflowList: async function (companyName) {
    const response = await axios.get(
      `${apiEndpoint.baseUri}${apiEndpoint.fetchWorkflowList}/${companyName}`
    );

    return response.data;
  },

  fetchWorkflowModuleList: async function () {
    const response = await axios.get(
      `${apiEndpoint.baseUri}${apiEndpoint.fetchWorkflowModuleList}`
    );

    return response.data;
  },

  // fetch workflow
  fetchWorkflow: async function (id, companyName) {
    const response = await axios.post(
      `${apiEndpoint.baseUri}${apiEndpoint.fetchWorkflow}/${companyName}/${id}`,
      {}
    );

    return response.data;
  },

  // update workflow
  updateWorkflow: async function (rawResponse, id, companyName) {
    const res = await axios.post(
      `${apiEndpoint.baseUri}${apiEndpoint.updateWorkflow}/${companyName}/${id}`,
      { workflow: rawResponse }
    );

    return res.data;
  },

  // create workflow
  createWorkflow: async function (data, workflowName, companyName) {
    const res = await axios.post(
      `${apiEndpoint.baseUri}${apiEndpoint.createWorkflow}/${companyName}`,
      { workflow: data, workflowName: workflowName }
    );

    return res.data;
  },
  // fetch workflow pdf
  fetchWorkflowPdf: async function (workflowName, companyName) {
    const res = await axios.get(
      `${apiEndpoint.baseUri}/get_pdfs_company_workflow/${workflowName}/${companyName}`
    );

    return res.data;
  },

  // fetch workflow fields
  fetchWorkflowFields: async function (id) {
    const res = await axios.post(`${apiEndpoint.baseUri}/fetch_fields_info`, {
      workflow_key: id,
    });

    return res.data;
  },

  // fetch static fields
  fetchStaticFields: async function () {
    const res = await axios.get(`${apiEndpoint.baseUri}/get_static_keys`);

    return res.data;
  },

  // fetch Esign options
  fetchEsignOptions: async function (workflowName, companyName) {
    const res = await axios.get(
      `${apiEndpoint.baseUri}/all_pdf_field/${companyName}/${workflowName}`
    );

    return res.data;
  },

  // Save workflow order
  saveWorkflowOrder: async function (data) {
    const res = await axios.post(
      `https://workflow.meon.co.in/save_workflow_order`,
      {
        ...data,
      }
    );

    return res.data;
  },

  // get workflow order
  getWorkflowOrder: async function (workflowKey) {
    const res = await axios.get(
      `https://workflow.meon.co.in/get_workflow_order/${workflowKey}`
    );

    return res.data;
  },

  // get otp fields
  getOTPFields: async function (companyName, workflowName) {
    const res = await axios.get(
      `${apiEndpoint.baseUri}/get_otp_fields/${companyName}/${workflowName}`
    );

    return res.data;
  },
};
