import React from "react";
import { Form, Col } from "react-bootstrap";

const Checkbox = (props) => {
  const { label, value, fieldName, fieldId, onChangeHandler, col } = props;
  return (
    <Form.Group className="mb-3" as={Col} md={col || "6"}>
      <Form.Label>{label}</Form.Label>
      <Form.Check
        className="custom-checkbox-lg"
        checked={value}
        onChange={(e) => onChangeHandler(e, fieldName, fieldId)}
      />
    </Form.Group>
  );
};

export default Checkbox;
