import axios from "axios";
import React, { useEffect, useState } from "react";
import { Col, Form, Row } from "react-bootstrap";

const Inputmodal = ({ showSelect, title, addNewHandler }) => {
  const [workflowList, setWorkflowList] = useState([]);
  const [selectedOption, setSelectedOption] = useState("select");
  const [value, setValue] = useState("");

  const [selectedId, setSelectedId] = useState(null);

  const handleChange = (event) => {
    let newValue = event.target.value.trim();

    // Convert uppercase letters to lowercase
    newValue = newValue.toLowerCase();

    // Remove special symbols and spaces, allow only underscores
    newValue = newValue.replace(/[^a-zA-Z0-9_]/g, "_");

    setValue(newValue);
  };

  const getExistingWorkflows = async () => {
    const workflowList = await axios.get(
      "https://kycuat.meon.co.in/fetch_all_workflows/meon"
    );
    setWorkflowList(workflowList.data.workflows);
  };

  useEffect(() => {
    if (showSelect) {
      getExistingWorkflows();
    }
  }, []);

  useEffect(() => {
    if (showSelect) {
      const item = workflowList.filter(
        (item) => item.workflowName === selectedOption
      );
      setSelectedId(item[0]?.work_flow_key);
      if (selectedOption !== "select" && selectedOption !== "others") {
        setValue(selectedOption);
      }
    }
  }, [selectedOption]);

  return (
    <div
      className="modal fade"
      id="inputModal"
      tabIndex={-1}
      aria-labelledby="inputModalLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="inputModalLabel">
              {title}
            </h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            />
          </div>
          <div className="modal-body">
            {showSelect && (
              <Row>
                <Form.Group className="mb-3" as={Col} md="12">
                  <Form.Select
                    value={selectedOption}
                    onChange={(e) => {
                      setSelectedOption(e.target.value);
                    }}
                  >
                    <option value={"select"}>Select</option>

                    {workflowList.map((option, index) => (
                      <option
                        id={option.work_flow_key}
                        key={index}
                        value={option.workflowName}
                      >
                        {option.workflowName}
                      </option>
                    ))}
                    <option value={"others"}>Others</option>
                  </Form.Select>
                </Form.Group>
              </Row>
            )}
            <input
              type="text"
              className="form-control"
              placeholder="Enter text"
              value={value}
              onChange={handleChange}
            />
          </div>

          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>

            <button
              type="button"
              className="btn btn-primary"
              data-bs-dismiss="modal"
              onClick={() =>
                addNewHandler(value, setValue, selectedOption, selectedId)
              }
            >
              Add
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Inputmodal;
