import React from "react";
import { Col, Form, Row } from "react-bootstrap";
import { selectDateOptions } from "../../../../../../appConfig";
import { capitalizeFirstLetter } from "../../../../../../helpers/utils";

const DateTypeValue = ({ item, onChangeHandler }) => {
  return (
    <Row>
      <Form.Group className="mb-3" as={Col} md={"4"}>
        <Form.Label>Date type</Form.Label>
        <Form.Select
          value={item?.dateType}
          onChange={(e) =>
            onChangeHandler(e, "dateType", "if", null, item.sectionId)
          }
        >
          {selectDateOptions.map((option, index) => (
            <option key={index} value={option}>
              {capitalizeFirstLetter(option)}
            </option>
          ))}
        </Form.Select>
      </Form.Group>

      <Form.Group className="mb-3" as={Col} md={"4"}>
        <Form.Label>Date Value</Form.Label>

        <Form.Control
          type={item?.dateType === "datepicker" ? "date" : "number"}
          value={item?.dateValue}
          onChange={(e) =>
            onChangeHandler(e, "dateValue", "if", null, item.sectionId)
          }
        />
      </Form.Group>
    </Row>
  );
};

export default DateTypeValue;
