import React from "react";
import { Col, Form, Row } from "react-bootstrap";
import { capitalizeFirstLetter } from "../../../../../helpers/utils";

const CheckBoxAndInput = (props) => {
  const {
    notifications,
    checkboxField,
    inputField,
    selectField,
    checkLabel,
    inputPlaceholder,
    onChangeHandler,
    workflowFieldsList,
  } = props;

  return (
    <Row>
      <Form.Group className="mb-3" as={Col} md="4">
        <Form.Check
          label={checkLabel}
          className="mb-2 fs-4 checkboxLabel"
          checked={notifications[checkboxField] || false}
          onChange={(e) => onChangeHandler(e, checkboxField)}
        />
      </Form.Group>
      <Form.Group as={Col} md="4">
        <Form.Control
          disabled={!notifications[checkboxField]}
          placeholder={inputPlaceholder}
          value={notifications[inputField] || ""}
          onChange={(e) => onChangeHandler(e, inputField)}
        />
      </Form.Group>
      <Form.Group as={Col} md="4">
        <Form.Select
          disabled={!notifications[checkboxField]}
          value={notifications[selectField] || ""}
          onChange={(e) => onChangeHandler(e, selectField)}
        >
          <option>Select</option>
          {workflowFieldsList &&
            workflowFieldsList.map((item, index) => (
              <option key={index} value={item}>
                {capitalizeFirstLetter(item)}
              </option>
            ))}
        </Form.Select>
      </Form.Group>
    </Row>
  );
};

export default CheckBoxAndInput;
