import React, { useEffect, useState } from "react";
import { Accordion, Col, Form, Row } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { workflowAPI } from "../../../../../../services/workflowApi";
import { errorToast } from "../../../../../../helpers/toast";
import { capitalizeFirstLetter } from "../../../../../../helpers/utils";

const OtpOptions = ({ otpExpiry, onChangeHandler, fieldId }) => {
  const [options, setOptions] = useState([]);
  let location = useLocation();
  const { key: workflowKey, workflowName } = location.state || {};
  const companyName = sessionStorage.getItem("companyName");

  const fetchOtpFields = async () => {
    try {
      if (companyName && workflowName) {
        const res = await workflowAPI.getOTPFields(companyName, workflowName);
        setOptions(res?.data);
      }
    } catch (err) {
      errorToast("Something went wrong!");
    }
  };

  useEffect(() => {
    fetchOtpFields();
  }, [companyName, workflowName]);

  return (
    <>
      <Row>
        <Form.Group as={Col} md="4">
          <Form.Label className="fs-6">Set Expiry</Form.Label>
          <Form.Check
            className="mb-3 fs-2"
            checked={otpExpiry?.isExpiryEnabled}
            onChange={(e) =>
              onChangeHandler(e, "otpExpiry", fieldId, "isExpiryEnabled")
            }
          />
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Label className=" fs-6">Time</Form.Label>
          <Form.Control
            disabled={!otpExpiry?.isExpiryEnabled}
            type="number"
            value={otpExpiry?.time}
            onChange={(e) => onChangeHandler(e, "otpExpiry", fieldId, "time")}
            placeholder="Time in second..."
          />
        </Form.Group>
        <Form.Group className="mb-3" as={Col} md="4">
          <Form.Label className=" fs-6">OTP Field</Form.Label>

          <Form.Select
            disabled={!otpExpiry?.time}
            value={otpExpiry?.otpField}
            onChange={(e) =>
              onChangeHandler(e, "otpExpiry", fieldId, "otpField")
            }
          >
            <option>Select</option>
            {options.map((option, index) => (
              <option key={index} value={option}>
                {capitalizeFirstLetter(option)}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
      </Row>
    </>
  );
};

export default OtpOptions;
